.time {
    color: white;
    font-size: 30px;
    border-radius: 50%;       /* Makes it round */
    padding: 15px;            /* Space inside the circle */
    text-align: center;       /* Center the text */
    background-color: #44178D; /* Optional */
    width: 60px;              /* Set width and height for a perfect circle */
    height: 60px;
    line-height: 30px;        /* Adjust line height to center text vertically */
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
    margin-left: auto;
}

.results {
    border-radius: 10px; 
    padding: 5px;
}

.headercode {
    display: flex;
    align-items: center;
    justify-content: space-between;
    align-content: center;
    margin-top: -50px;
}

.profile_container{
    display: flex;
    flex-grow: 1;
    flex-basis: 0;
}

.timeholder{
    display: flex;
    flex-grow: 1;
    flex-basis: 0;
}

.profile {
    /* top: 0px;
    left: 20px;
    position: fixed; */
    background-color: #44178D;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-left: 20px;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 30px;
    justify-self : flex-start;
}



.name {
    color: white;
    font-size: 30px;
}

.profile-avatar {
    height: 60px;
    margin-right: 15px;
    border-radius: 5px;
}

.leaderboard-client-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:20px;
    flex-direction: column;
}

.ranktext {
    color: white;
    font-size: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: row; /* or column, depending on layout */
}

.meme-image {
    height: 200px;
}

.scrollable {
    overflow: auto;
    max-height: 100%; /* Optional */
  }

.score-text {
    color:white;
    font-size: 30px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.vscode-navbar {
    border-radius: 0px;
    margin-bottom: -20px;
    display: flex;
}

.code-container {
    display: flex;
    justify-content: flex-start; /* this fixes the issue */
    align-items: flex-start;
    margin-top: -40px;
}

@media (max-width: 1300px) {
    .code-container {
      flex-direction: column;
      margin-left: 0px;
      gap: 0px;
      padding: 0px;
      margin-left: 0px;
    }
    .compiler-output {
        gap: 2000px;
    }
  }

.compiler-output {
    margin-left: 10px;
    height: 520px;
}

.navbar-button-selected {
    font-size: 15px;

    height: 45px;
    margin-top: -10px;
    background-color:  #393939;
    border-radius: 0px;
    border: none;
}

.navbar-button-unselected {
    font-size: 15px;
    height: 45px;
    margin-top: -10px;
    background-color:  #121212;
    border-radius: 0px;
    border: none;
}

.next-button {
    height: 60px;
    font-size: 30px;
    margin-top: 40px;
    margin-left: 5px;
}

.codehoot-title:hover {
    cursor: pointer;
}